header {
    background-color: #ffffff;
    height: 54px;
    z-index: 2000;
    /* box-shadow: rgba(60, 64, 67, 0.08) 0px 1px 1px 0px, rgba(60, 64, 67, 0.06) 0px 1px 3px 1px; */
    /* border-bottom: 1px solid #eeeeee; */
    /* box-shadow: rgba(33, 35, 38, 0.1) 0px 10px 10px -10px; */
    border-bottom: 1px solid #dadce0;
    padding: 0 2%;
    margin-top: 10px;
}

h1 {
    margin: 0;
    font-weight: 500;
    font-size: 20px;
    /* color: #6938EF; */
    /*color: #FF1053;*/
    padding: 8px 0;
}

/* 
#DBFE87 
FF1053 folly pink
old pointer color: background: #9a68d3;

for dark mode: #443850
*/
.boardName {
    display: flex;
}
.btnEditBoardName {
    flex: 1;
    background-image: url('/src/assets/icons/edit_dark.svg');
    opacity: 0;
    background-color: #ffffff;
    background-repeat: no-repeat;
    background-position: 14px 12px;
}

div.boardName:hover .btnEditBoardName {
    opacity: 0.5;
}




@media only screen and (min-width: 769px) {
    header {
        padding: 0 2%;
    }
}

@media only screen and (max-width: 768px) {
    header {
        padding: 0 10px;
    }
}